import React from 'react';

function CodigoDeConducta() {
  return (
    <div>
        <div><h1>Código de Conducta</h1><h2>Propósito</h2><p>Un objetivo principal de todas las conferencias y grupos de usuarios que se refieran a este Código de Conducta es ser inclusivos para la mayor cantidad de personas, con la mayor variedad de orígenes y conocimientos posibles. Como tal, nos comprometemos a ofrecer un ambiente amigable, seguro y acogedor para todos, sin importar género, orientación sexual, capacidad, etnia, estado socio-económico, religión o creencia.</p><p>Este Código de Conducta expresa nuestras expectativas con respecto a quienes participan de nuestra comunidad, así como también las consecuencias de comportamientos inaceptables.</p><p>Invitamos a todos los que participan de nuestros eventos a ayudarnos a crear experiencias seguras y positivas para todos.</p><h2>Ciudadanía de Código / Cultura / Tecnología abierta</h2><p>Un objetivo suplementario de este Código de Conducta es incrementar la ciudadanía de código/cultura/tecnología abierta, incentivando a sus participantes a reconocer y fortalecer las relaciones entre nuestras acciones y sus efectos en nuestra comunidad.</p><p>Las comunidades son espejo de las sociedades en las cuales existen y las acciones positivas son esenciales para contrarrestar las diversas formas de desigualdad y abuso de poder que existen en una sociedad.</p><p>Si Ud. ve a alguien haciendo un esfuerzo extra por asegurarse que nuestra comunidad sea acogedora, amistosa e incentiva a todos los que participan en ella a contribuir de manera completa, nos gustaría saberlo.</p><h2>Comportamiento esperado</h2><ul><li>Participe de un modo auténtico y activo. Al hacerlo contribuye a la salud y longevidad de esta comunidad.</li><li>Ejercite la consideración y el respeto en su discurso y en sus acciones.</li><li>Intente colaborar para evitar conflictos.</li><li>Absténgase de discursos o comportamientos despectivos, discriminatorios o abusivos.</li><li>Sea consciente de su entorno y de los/as otros/as participantes. Alerte a los líderes de la comunidad si nota alguna situación peligrosa, alguien sufriendo una situación comprometedora o violaciones de este Código de Conducta, incluso si parecieran poco importantes.</li></ul><h2>Comportamiento inaceptable</h2><p>Comportamientos inaceptables incluyen: discursos o acciones intimidantes, acosadores, abusivos, discriminatorios, despectivos o degradantes. El alcance incluye a todos/as los/as participantes de nuestra comunidad online en todos los eventos y comunicaciones personales llevadas a cabo en el contexto de las actividades de nuestra comunidad. Los lugares en donde se lleven a cabo eventos de la comunidad pueden ser compartidos con gente ajena a la misma, por favor sea respetuoso hacia las autoridades de dichos lugares.</p><p>Acoso incluye: daño o perjurio verbal o escrito relacionado con género, orientación sexual, raza, religión, incapacidad; uso inapropiado de imágenes de desnudez en espacios públicos (incluyendo presentaciones digitales); intimidación deliberada, acecho o persecución; fotografías o grabaciones abusivas; interrupción sostenida de presentaciones u de otros eventos; contacto físico inapropiado y atención sexual no deseada.</p><h2>Consecuencias del comportamiento inaceptable</h2><p>No serán tolerados los comportamientos inaceptables de parte de cualquier miembro de la comunidad, incluidos patrocinadores y aquellos/as con autoridad para tomar decisiones. Se espera de todos/as los/as miembros el inmediato cumplimiento al solicitársele suspender comportamientos inaceptables.</p><p>Si un miembro incurre en comportamientos inaceptables los organizadores de la comunidad pueden sancionarlo de cualquier forma que se considere apropiada, incluyendo la suspensión temporal o permanente de su participación en la comunidad, sin previo aviso (y sin reintegro del dinero, en caso de ser un evento pago).</p><h2>Si es testigo o víctima de comportamiento inaceptable</h2><p>Si Ud. es víctima o testigo de comportamiento inaceptable, o tiene cualquier otra preocupación o problema, por favor notifíquelo a un organizador de la comunidad tan pronto como sea posible. Puede encontrar una lista de los organizadores a contactar para cada una de las comunidades que apoyen este código de conducta al final de esta página.</p><p>Además, los organizadores de la comunidad se encuentran disponibles para ayudar a los miembros a contactar a las fuerzas policiales locales o, de lo contrario, para ayudar a sentirse seguros a aquellos que hayan sido víctimas de comportamiento inaceptable. En el contexto de eventos presenciales, los organizadores también proveerán escolta a las personas que hayan sufrido dichas experiencias.</p><h2>Desagravios</h2><p>Si Ud. siente que ha sido falsa o injustamente acusado/a de violar este Código de Conducta, se le sugiere notificar a uno de los organizadores del evento con una descripción concisa del agravio. El mismo será contemplado y resuelto de acuerdo a las políticas existentes.</p><h2>Alcance</h2><p>Se espera que todos los participantes de la comunidad (colaboradores/as -pagos o no-, patrocinadores y otros/as invitados/as) se atengan a este Código de Conducta en todos los lugares de encuentro de la comunidad –online o en persona– así como en todas las comunicaciones uno-a-uno relacionadas con actividades de la comunidad.</p><h2>Información de contacto</h2><ul><li>Alejandro Oviedo, <a href="mailto:alejandro.oviedo.g@gmail.com">alejandro.oviedo.g@gmail.com</a></li><li>Camila Cepeda, <a href="mailto:camicepe@gmail.com">camicepe@gmail.com</a></li><li>Cristhian Duran, <a href="mailto:durancristhian@gmail.com">durancristhian@gmail.com</a></li><li>Mateo Silguero, <a href="mailto:mfsilguero@gmail.com">mfsilguero@gmail.com</a></li><li>Sergio Rafael Gianazza, <a href="mailto:sgianazza@gmail.com">sgianazza@gmail.com</a></li></ul><h2>Licencia y atribución</h2><p>El Berlin Code of Conduct se encuentra distribuido bajo una licencia Creative Commons Attribution-ShareAlike. Está basado en el <a href="https://pdxruby.org/codeofconduct" target="_blank" rel="nofollow noopener noreferrer">pdx.rb code of conduct</a>, que también es distribuido bajo la misma licencia.</p></div>
    </div>
  );
}

export default CodigoDeConducta;

